<template>
  <!--  -->
  <div id="my-manage">
    <div class="container-l">
      <e-tree @treeClick="treeClick" :data="treeArr" />
    </div>
    <div id="container-main">
      <div class="container-nav">
        <div class="container-form">
          <el-form :inline="true" ref="addForm" :model="form" :rules="rules" class="demo-form-inline">
            <el-form-item label="">
              <el-input v-model="form.nickName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.phone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.status" placeholder="">
                <el-option label="" :value=0></el-option>
                <el-option label="" :value=1></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit"></el-button>
              <el-button @click="onClear"></el-button>
            </el-form-item>
          </el-form>
          <el-button type="primary" @click="show(null, '')"></el-button>
        </div>

        <el-table
          :row-class-name="tableRowClassName"
          @row-click="rowClick"
          ref="multipleTable"
          :data="resultList"
          tooltip-effect="dark"
          style="width: 100%"
          v-if="resultList.length !== 0"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="" width="120">
            <template scope="scope">{{ scope.$index + 1 }}</template>
          </el-table-column>
          <el-table-column prop="nickName" label="" width="120">
          </el-table-column>
          <el-table-column prop="email" label="email" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="phone" label="" show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="department"
            label=""
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="createTime"
            label=""
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column prop="starts" label="" show-overflow-tooltip>
            <template scope="scope">
              <p>
                <el-tag v-if="scope.row.status === 0" type="success"></el-tag>
                <el-tag v-else type="error"></el-tag>
              </p>
            </template>
          </el-table-column>
          <el-table-column prop="starts" label="" show-overflow-tooltip>
            <template scope="scope" class="link">
              <a @click="show(scope.row, '')" class="link-item"></a>
              <a @click="show(scope.row, '')" class="link-item err"></a>
            </template>
          </el-table-column>
        </el-table>
        <my-empty v-else />
        <!--
              
          -->
        <el-dialog
          :title="title"
          :visible.sync="isShow"
          width="40%"
        >
          <el-form ref="addForm" :model="addForm" id="form" :rules="rules">
            <el-form-item
              class="form-item"
              label=""
              :label-width="formLabelWidth"
              prop="nickName"
            >
              <el-input v-model="addForm.nickName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item
              class="form-item"
              label=""
              :label-width="formLabelWidth"
              prop="roleId"
            >
              <el-select
                v-if="title === ''"
                class="form-item"
                v-model="addForm.roleId "
                placeholder=""
              >
                <el-option
                  :label="item.label"
                  :value="item.value"
                  v-for="item in roleList"
                ></el-option>
              </el-select>
              <el-select
                v-else
                class="form-item"
                v-model="addForm.roleId "
                :placeholder="addForm.department"
              >
                <el-option
                  :label="item.label"
                  :value="item.value"
                  v-for="item in roleList"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              class="form-item"
              label=""
              required
              :label-width="formLabelWidth"
            >
              <el-cascader
                v-model="deptId"
                :options="treeArr"
                :props="defaultProps"></el-cascader>
            </el-form-item>
            <el-form-item
              class="form-item"
              label=""
              :label-width="formLabelWidth"
              prop="sex"
            >
              <el-select v-model="addForm.sex" placeholder="">
                <el-option
                  :label="item.label"
                  :value="item.value"
                  v-for="item in sexList"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              class="form-item"
              label=""
              type="email"
              :label-width="formLabelWidth"
              prop="username"
            >
              <el-input v-model="addForm.username" autocomplete="off">
              </el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-radio-group v-model="addForm.status">
                <el-radio :label="0"></el-radio>
                <el-radio :label="1"></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item
              class="form-item"
              label=""
              prop="phone"
              :label-width="formLabelWidth"
            >
              <el-input v-model="addForm.phone" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item
              v-if="title !== ''"
              class="form-item"
              label=""
              :label-width="formLabelWidth"
              prop="password"
            >
              <el-input
                v-model="addForm.password"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              class="form-item"
              label=""
              :label-width="formLabelWidth"
            >
              <el-input
                type="email"
                v-model="addForm.email"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="isShow = false"> </el-button>
            <!--            <el-button-->
            <!--                v-if="type"-->
            <!--                type="primary"-->
            <!--                @click="upDataAddForms('addForm')"-->
            <!--            > -->
            <!--            </el-button-->
            <!--            >-->
            <el-button type="primary" @click="addForms('addForm')"
            > 
            </el-button
            >
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import eTree from "@/components/eTree";
import {
  addUser,
  deleteRoleList,
  getUserAll,
  getOrganizeList,
  upDataRoleList,
  getOrganizeId, deleteUser
} from "@/newwork/system-colltroner";
import { fn } from "@/uti";
import myEmpty from "@/newwork/myEmpty";

export default {
  name: "serve-manage",
  components: {
    eTree,
    myEmpty
  },
  data() {
    return {
      form: {
        nickName: "",
        phone: ""
      },
      addForm: {
        deptId: "",//
        email: "", //email
        nickName: "",//
        password: "",//
        phone: "", //
        roleId: "", // 'id'
        sex: "",  // / 
        status: 0,//
        pageSize: 10,
        pageNum: 1,
        username: "" //
      },
      row: {},
      type: "",
      title: "",
      deptIdList: [],
      isShow: false,
      resultList: [],
      departmentList: [],
      deptId: "",
      formLabelWidth: "120px",
      roleList: [
        { label: "", value: 0 },
        { label: "", value: 1 },
        { label: "", value: 2 }
      ],
      timer: {},
      treeArr: [],
      sexList: [
        { label: "", value: 0 },
        { label: "", value: 1 }
      ],
      rules: {
        nickName: [
          { required: true, message: "", trigger: "change" }
        ],
        roleId: [
          { required: true, message: "", trigger: "change" }
        ],
        deptId: [
          { required: false, message: "", trigger: "change" }
        ],
        username: [
          { required: true, message: "", trigger: "change" }
        ],
        password: [
          { required: true, message: "", trigger: "change" }
        ],
        phone: [
          { required: true, message: "", trigger: "change" }
        ],
        status: [
          { required: false, message: "", trigger: "change" }
        ],
        sex: [
          { required: false, message: "", trigger: "change" }
        ]
      }
    };
  },

  methods: {
    onSubmit() {
      this.getUserAll(this.form);
    },
    treeClick(a) {
      this.form = this.$store.state.formPage;
      this.form.deptIds = a.deptId;
      this.getUserAll(this.form);
    },
    addForms(addForm) {
      //  
      this.$refs.addForm.validate((valid) => {
        if (valid && this.addForm.deptId.length !== 0) {
          if (this.title === "") {
            addUser(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.getUserAll(this.$store.state.formPage);
                this.$message.success("");
              } else {
                this.$message.error(res.data.msg);
              }
            }).catch(e => {
              this.$message.error(e);
            });
            this.isShow = false;
          } else {
            upDataRoleList(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.getUserAll(this.$store.state.formPage);
                this.$message.success("");
              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    onClear() {
      this.resetForm();
      this.getUserAll(this.form);
    },
    show(row, title) {
      this.title = title;
      if (this.title === "") {
        this.isShow = true;
        this.$refs.addForm.clearValidate();
        this.addForm = this.$options.data().addForm;
        getOrganizeList(this.form).then(res => {
          this.deptIdList = res.data.data;
        }).catch(e => {
          this.$message.error(e);
        });
      } else if (title === "") {
        this.addForm = row;
        this.isShow = true;
      } else if (title === "") {
        deleteUser(row.userId).then(res => {
          if (res.data.code === 200) {
            this.$message.success("!");
            this.getUserAll(this.$store.state.formPage);
          } else {
            this.$message.error(res.data.msg);
          }
        }).catch(e => {
          this.$message.error(e);
        });
      }
    },
    resetForm() {
      this.$refs.addForm.resetFields();
    },

    rowClick(row) {
      this.row = row;
    },
    tableRowClassName({ row, rowIndex }) {
      //row
      row.index = rowIndex;
    },
    getUserAll(formPage) {
      getUserAll(formPage).then(res => {
        this.resultList = res.data.data.records;
        console.log(res.data);

        this.$store.dispatch("total", res.data.data.total);
        //id 
        this.resultList.forEach((item, i) => {
          if (item.hasOwnProperty("deptId")) {
            getOrganizeId(item.deptId).then(res => this.$set(this.resultList[i], "department", res.data.data.deptName));
          }
        });
      }).catch(e => this.$message.error(e));
    }
  },
  created() {
    this.getUserAll(this.$store.state.formPage);
    getOrganizeList().then(res => {
      this.treeArr = fn(res.data.data);

      console.log(this.treeArr);
    });
    this.form = this.$store.state.formPage;
  },
  computed: {
    getterDeptIdList() {
      return fn(this.deptIdList);
    },
    // roleId(){
    //   if(this.addForm.roleId.length !== 0){
    //     return parseInt(this.addForm.roleId)
    //   }else {
    //     return   = ''
    //   }
    // },
    defaultProps() {
      return {
        children: "children",
        label: "deptName",
        value: "deptId",
        expandTrigger: "hover"
      };
    }
  },


  watch: {
    deptId(val) {
      this.addForm.deptId = this.deptId[this.deptId.length - 1];
    },
    timer(val) {
      console.log(val);
      this.addForm.createTime = val[0];
      this.addForm.endTime = val[1];
    }
  },
  mounted() {
    this.$bus.$on("pageChange", () => {
      this.getUserAll(this.$store.state.formPage);
    });
  }
};
</script>

<style scoped>
#my-manage {
  display: flex;
}

.container-l {
  width: 200px;
}

.container-form {
  display: flex;
  height: 40px;
  justify-content: space-between;
}

#container-main {
  padding-left: 30px;
  flex: 1;
}

#form {
  display: flex;
  flex-wrap: wrap;
}

#form > .form-item {
  width: 48%;
}

.link-item {
  margin: 0 10px;
  cursor: pointer;
}

.err {
  color: red;
}

.warning {
  color: gold;
}
</style>
